@import "../../global.scss";
.portfolio{
    background-color: black;
    color:white;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    h1{
        font-size: 50px;

        @include mobile{
            font-size: 20px;
        }
    }
    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        @include mobile{
            font-size: 20px;
            flex-wrap: wrap;
            justify-content: center;
            margin: 10px 0;
        }

    }

    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        overflow:hidden;
        @include mobile{
            width: 100%;;
        }

        .item{
            width: 220px;
            height:170px;
            border-radius:20px;
            border: 1px solid #fff;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;

            @include mobile{
                width: 130px;
                height: 150px;
            }
            

            h3{
                position: absolute;
                font-size: 20px;
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
                align-items: center;
            }
            &:hover{
                background-color: $mainColor;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
}